:root {
    //fonts
    --ff:'Josefin Sans', sans-serif;
    //font sizes
    // 14 16 18 32
    // 10 12 22
    --fs-xlarge: 2rem;
    --fs-large: 1.375rem;
    --fs-body: 1.125rem;
    --fs-med: 1rem;
    --fs-small: .875rem;
    --fs-xsmall: .75rem;
    --fs-xxsmall: .625rem;
    //font weights
    --fw-reg:400;
    --fw-bold:700;
    

    //colours
    //primary
    --accent-clr-BrightBlue: hsl(220, 98%, 61%);
    // --checkbox-bg-clr: linear-gradient(to right, var(--aqua) 40%, var(--pink) 60%);
    // --aqua:hsl(192, 100%, 67%);
    // --pink:hsl(280, 87%, 65%);

    //light theme
    --VeryLightGray: hsl(0, 0%, 98%);
    --VeryLightGrayishBlue: hsl(236, 33%, 92%);
    --LightGrayishBlue: hsl(233, 11%, 84%);
    --DarkGrayishBlue: hsl(236, 9%, 61%);
    --VeryDarkGrayishBlue: hsl(235, 19%, 35%);

    //dark theme
    --VeryDarkBlue: hsl(235, 21%, 11%);
    --VeryDarkDesaturatedBlue: hsl(235, 24%, 19%);
    --LightGrayishBlue: hsl(234, 39%, 85%);
    --LightGrayishBlue-hover: hsl(236, 33%, 92%);
    --DarkGrayishBlue: hsl(234, 11%, 52%);
    --VeryDarkGrayishBlue: hsl(233, 14%, 35%);
    --VeryDarkGrayishBlue: hsl(237, 14%, 26%);
}

:root[data-theme="light"] {
    --bg-clr: var(--VeryLightGray);
    --list-bg-clr: hsl(hsla(0, 0%, 100%, 1));

    --text-clr-xlight: var(--DarkGrayishBlue);
    --text-clr-light: var(--DarkGrayishBlue);
    --text-clr: var(--VeryDarkGrayishBlue);
    
}

:root[data-theme="dark"] {
    --bg-clr: var(--VeryDarkBlue);
    --list-bg-clr: var(--VeryDarkDesaturatedBlue);

    --text-clr-xlight: var(--DarkGrayishBlue);
    --text-clr-light: var(--LightGrayishBlue);
    --text-clr: var(--LightGrayishBlue-hover);
    
}


